.contact {
  background: white;
  padding-bottom: 100px;
}
.contact .container5 .title-section {
  text-align: center;
  padding-bottom: 40px;
}
.contact .container5 .contact-container {
  display: flex;
  justify-content: space-between;
}
.contact .container5 .contact-container .contact-left {
  width: 100%;
}
.contact .container5 .contact-container .contact-left form {
  display: flex;
  flex-direction: column;
}
.contact .container5 .contact-container .contact-left form label {
  font-weight: 500;
}
.contact .container5 .contact-container .contact-left form input, .contact .container5 .contact-container .contact-left form textarea {
  outline: none;
  padding: 17px;
  border: none;
  margin: 10px 0 20px 0;
  border-bottom: 2px solid black;
}
.contact .container5 .contact-container .contact-left form .submit {
  background: var(--colorThemeOne3, linear-gradient(90deg, rgb(0, 135, 38) 0%, rgb(50, 185, 44) 100%));
  width: 130px;
  font-size: 18px;
  padding: 9px;
  outline: none;
  border: none;
  border-radius: 7px;
  color: white;
  transition: transform 0.1s ease-in-out;
  cursor: pointer;
}
.contact .container5 .contact-container .contact-left form .submit:hover {
  transform: scale(1.02);
}
.contact .container5 .contact-container .contact-right {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.contact .container5 .contact-container .contact-right .details {
  margin-bottom: 30px;
}
.contact .container5 .contact-container .contact-right .details span {
  font-size: 18px;
}
.contact .container5 .contact-container .contact-right .details .icon-container {
  display: flex;
  -moz-column-gap: 10px;
       column-gap: 10px;
  align-items: flex-end;
  padding: 10px 0;
  text-decoration: none;
  color: rgb(50, 50, 50);
}
.contact .container5 .contact-container .contact-right .details .icon-container img {
  width: 25px;
}
.contact .container5 .contact-container .contact-right .details .icon-container label {
  font-weight: 500;
  cursor: pointer;
}
.contact .container5 .contact-container .contact-right .social span {
  font-size: 18px;
}
.contact .container5 .contact-container .contact-right .social .icon-container {
  display: flex;
  -moz-column-gap: 10px;
       column-gap: 10px;
  align-items: flex-end;
  padding: 10px 0;
  text-decoration: none;
  color: rgb(50, 50, 50);
}
.contact .container5 .contact-container .contact-right .social .icon-container img {
  width: 25px;
}
.contact .container5 .contact-container .contact-right .social .icon-container label {
  font-weight: 500;
  cursor: pointer;
}

@media only screen and (min-width: 0px) {
  .contact {
    padding-top: 80px;
  }
  .contact .container5 {
    padding: 0 18px;
  }
  .contact .container5 .title-section {
    font-size: 25px;
  }
  .contact .container5 .contact-container {
    flex-direction: column;
    row-gap: 50px;
  }
  .contact .container5 .contact-container .contact-left form .submit {
    font-size: 15px;
    width: 140px;
    height: 35px;
  }
}
@media only screen and (min-width: 700px) {
  .contact .container5 {
    padding: 0 40px;
  }
}
@media only screen and (min-width: 800px) {
  .contact .container5 .contact-container {
    flex-direction: row;
    -moz-column-gap: 70px;
         column-gap: 70px;
    justify-content: space-between;
  }
}
@media only screen and (min-width: 900px) {
  .contact {
    padding-top: 60px;
  }
  .contact .container5 .title-section {
    font-size: 35px;
    padding-bottom: 80px;
  }
}/*# sourceMappingURL=footer.css.map */